import { useEffect, useRef } from "react";
import { useDispatch, useSelector } from "react-redux";

import { setIsConnected, setStreamActive } from "../../redux/webrtcSlice";

export const StreamViewer = () => {
  const dispatch = useDispatch();
  const videoRef = useRef(null);
  const { isConnected, streamActive } = useSelector((state) => state.webrtc);

  useEffect(() => {
    let pc = null;
    let ws = null;

    const setupConnection = () => {
      pc = new RTCPeerConnection({
        iceServers: [{ urls: "stun:stun.l.google.com:19302" }],
      });

      const protocol = window.location.protocol === "https:" ? "wss" : "ws";
      ws = new WebSocket(`${protocol}://192.168.0.103:8080`);

      ws.onopen = () => {
        console.log("‚úÖ WebSocket –ø—ñ–¥–∫–ª—é—á–µ–Ω–æ");
        dispatch(setIsConnected(true));
      };

      ws.onmessage = async (event) => {
        try {
          const message = JSON.parse(event.data);
          if (message.offer) {
            await pc.setRemoteDescription(
              new RTCSessionDescription(message.offer)
            );
            const answer = await pc.createAnswer();
            await pc.setLocalDescription(answer);
            ws.send(JSON.stringify({ answer }));
          } else if (message.iceCandidate) {
            await pc.addIceCandidate(new RTCIceCandidate(message.iceCandidate));
          }
        } catch (error) {
          console.error("‚ùå –ü–æ–º–∏–ª–∫–∞ –ø—Ä–∏ –æ–±—Ä–æ–±—Ü—ñ –ø–æ–≤—ñ–¥–æ–º–ª–µ–Ω–Ω—è:", error);
        }
      };

      pc.ontrack = (event) => {
        if (videoRef.current) {
          videoRef.current.srcObject = event.streams[0];
          dispatch(setStreamActive(true));
        }
      };

      pc.onicecandidate = (event) => {
        if (event.candidate) {
          ws.send(JSON.stringify({ iceCandidate: event.candidate }));
        }
      };

      ws.onclose = () => {
        console.log("‚ö†Ô∏è WebSocket –∑'—î–¥–Ω–∞–Ω–Ω—è –∑–∞–∫—Ä–∏—Ç–æ");
        dispatch(setIsConnected(false));
        dispatch(setStreamActive(false));
      };
    };

    if (!streamActive && isConnected) {
      setupConnection();
    }

    return () => {
      pc?.close();
      ws?.close();
    };
  }, [dispatch, isConnected, streamActive]);

  return <video ref={videoRef} autoPlay playsInline />;
};
